import * as React from "react";
import { IconButton, IIconProps, initializeIcons } from "office-ui-fabric-react";

export default class Messages extends React.Component {
  scrollToBottom = () => {
    this.messagesEnd.scrollIntoView({ behavior: "smooth" });
  };

  componentDidMount() {
    this.scrollToBottom();
  }

  componentDidUpdate() {
    this.scrollToBottom();
  }
  render() {
    const { messages, editMessageHelper, currentWorkingUser } = this.props;
    const properTime = str => str.substring(0, str.length - 6) + str.substring(str.length - 3);
    const messagesList = messages.map((message, index) => (
      <div className="container" key={index}>
        <small>{message.authorName}</small>
        <div className="message-body">
          <div className="message-content">{message.content}</div>
          {currentWorkingUser.email === message.authorEmail ? (
            <IconButton
              iconProps={{ iconName: "Edit" }}
              onClick={() => {
                editMessageHelper(message.content, index);
              }}
            />
          ) : (
            <div />
          )}
        </div>
        <span className="time-right">{properTime(message.creationDate.toLocaleTimeString())}</span>
      </div>
    ));
    return (
      <div className="messages">
        {messagesList}
        <div
          style={{ float: "left", clear: "both" }}
          ref={el => {
            this.messagesEnd = el;
          }}
        ></div>
      </div>
    );
  }
}
