import * as React from "react";
import { TextField } from "office-ui-fabric-react";
import { DefaultButton, MessageBar, MessageBarType, IconButton } from "office-ui-fabric-react";
import Messages from "./Messages";
import AnotherTest from "./AnotherTest";
import { HotKeys } from "react-hotkeys";

const keyMap = {
  new: "command+shift+n",
  esc: "escape",
  delete: "command+shift+d"
};

export default class DiscussTab extends React.Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
      listItems: [],
      messages: [],
      editMode: false,
      currentRange: "",
      newComment: false,
      text: "",
      allRanges: {},
      cursor: 0,
      savedCursor: 0,
      editIndex: -1,
      currentWorkingUser: {}
    };
    this.textInput = React.createRef();
    this.loadComments = this.loadComments.bind(this);
    this.loadmessages = this.loadmessages.bind(this);
    this.respondToMessages = this.respondToMessages.bind(this);
    this.setText = this.setText.bind(this);
    this.createComments = this.createComments.bind(this);
    this.deleteComments = this.deleteComments.bind(this);
    this.editMessage = this.editMessage.bind(this);
    this.editMessageHelper = this.editMessageHelper.bind(this);
    this.moveRefToComments = this.moveRefToComments.bind(this);
    this.handleKeyDown = this.handleKeyDown.bind(this);
    this.getCurrentWorkingUser = this.getCurrentWorkingUser.bind(this);
  }

  async getCurrentWorkingUser() {
    try {
      await Excel.run(async context => {
        const range = context.workbook.getSelectedRange().load();
        const worksheet = context.workbook.worksheets.load();
        const comments = context.workbook.comments.load();
        await context.sync();
        const currentWorksheet = worksheet.items[0].name;
        const tempAddress = currentWorksheet + "!ZZ999";
        comments.add(tempAddress, "hi");
        const tempComment = comments.getItemByCell(tempAddress).load();
        await context.sync();
        this.setState({
          currentWorkingUser: { name: tempComment.authorName, email: tempComment.authorEmail },
          currentRange: range.address
        });
        tempComment.delete();
        await context.sync();
        // console.log(this.state.currentWorkingUser);
      });
    } catch (error) {
      console.log(error);
    }
  }
  handleKeyDown(e) {
    const { cursor, listItems } = this.state;
    let range = listItems[cursor].sheet + "!" + listItems[cursor].cell;
    if (e.key === "ArrowUp" && cursor > 0) {
      this.setState(prevState => ({
        cursor: prevState.cursor - 1
      }));
    } else if (e.key === "ArrowDown" && cursor < listItems.length - 1) {
      this.setState(prevState => ({
        cursor: prevState.cursor + 1
      }));
    } else if (e.key === "Enter") {
      this.loadmessages(range);
      const tempCursor = this.state.cursor;
      this.setState({
        savedCursor: tempCursor,
        cursor: -1
      });
    }
  }
  setText(e) {
    this.setState({
      text: e.target.value
    });
  }
  async createComments() {
    try {
      await Excel.run(async context => {
        const range = context.workbook.getSelectedRange().load();
        await context.sync();

        this.setState({
          currentRange: range.address,
          newComment: true,
          messages: []
        });

        if (this.state.allRanges[range.address]) {
          this.loadmessages(range.address);
        } else {
          this.loadComments();
        }
      });
    } catch (error) {
      console.error(error);
    }
    this.textInput.current.focus();
  }
  async loadComments() {
    try {
      await Excel.run(async context => {
        OfficeExtension.config.extendedErrorLogging = true;

        let comments = context.workbook.comments;
        const viewComments = comments.load();
        await context.sync();

        let arr = [];
        let allRanges = {};
        for (let i = 0; i < viewComments.items.length; i++) {
          let comment = viewComments.items[i];
          let replies = comment.replies.load();
          let commentLocation = comment.getLocation().load();

          await context.sync();
          if (commentLocation.address === this.state.currentRange) this.setState({ cursor: i });

          allRanges[commentLocation.address] = true;
          let locationArr = commentLocation.address.split("!");
          arr.push({
            primaryText: comment.content,
            user: comment.authorName,
            date: comment.creationDate,
            count: replies.items.length + 1,
            sheet: locationArr[0],
            cell: locationArr[1]
          });
        }

        this.setState({
          listItems: arr,
          allRanges
        });
      });
    } catch (error) {
      console.log("THIS IS THE ERROR");
      console.error(error);
      this.loadComments();
    }
  }
  async deleteComments() {
    try {
      await Excel.run(async context => {
        const range = context.workbook.getSelectedRange();
        context.workbook.comments.getItemByCell(range).delete();
        this.setState({
          messages: [],
          newComment: true,
          text: "",
          savedCursor: 0,
          cursor: 0
        });
        await context.sync();
        this.loadComments();
      });
    } catch (error) {
      console.error(error);
    }
    // this.moveRefToComments();
    this.textInput.current.focus();
  }
  async loadmessages(range, e) {
    try {
      await Excel.run(async context => {
        this.setState({
          newComment: false
        });
        let comments = context.workbook.comments.getItemByCell(range);
        comments.getLocation().select();
        const viewComments = comments.load();
        const viewReplies = comments.replies.load();
        await context.sync();

        let messagesArr = [];
        messagesArr.push(viewComments);
        viewReplies.items.forEach(reply => {
          messagesArr.push(reply);
        });

        this.setState({
          messages: messagesArr,
          currentRange: range
        });
      });
    } catch (error) {
      console.error(error);
    }
    this.textInput.current.focus();
  }
  async respondToMessages() {
    if (this.state.text.length) {
      try {
        await Excel.run(async context => {
          if (this.state.newComment) {
            let comments = context.workbook.comments;
            comments.add(this.state.currentRange, this.state.text);
            this.setState({
              newComment: false
            });
            this.loadComments();
          } else {
            let comments = context.workbook.comments.getItemByCell(this.state.currentRange);
            comments.replies.add(this.state.text);
          }
          await context.sync();
          this.loadmessages(this.state.currentRange);
          this.setState({
            text: ""
          });
        });
      } catch (error) {
        console.error(error);
      }
    }
  }
  editMessageHelper(message, index) {
    this.setState({
      editMode: true,
      text: message,
      editIndex: index
    });
    this.textInput.current.focus();
  }
  async editMessage() {
    try {
      await Excel.run(async context => {
        let comments = context.workbook.comments.getItemByCell(this.state.currentRange);
        let messages = null;

        if (this.state.editIndex === 0) {
          messages = comments.load();
          await context.sync();
          messages.set({ content: this.state.text });
        } else {
          messages = comments.replies.load();
          await context.sync();
          messages.items[this.state.editIndex - 1].set({ content: this.state.text });
        }
        this.loadmessages(this.state.currentRange);
        this.setState({
          text: "",
          editMode: false,
          editIndex: -1
        });
      });
    } catch (error) {
      console.error(error);
    }
    this.textInput.current.focus();
  }
  componentDidMount() {
    this.getCurrentWorkingUser();
    this.loadComments();
    this.moveRefToComments();
  }
  moveRefToComments() {
    document.getElementById("main").focus();
    this.setState({
      cursor: this.state.savedCursor
    });
  }

  render() {
    const properTime = str => str.substring(0, str.length - 6) + str.substring(str.length - 3);
    const messagesList = this.state.messages.map((message, index) => (
      <div className="container" key={index}>
        <small>{message.authorName}</small>
        <div style={{ paddingLeft: 20 }}>{message.content}</div>
        <span className="time-right">{properTime(message.creationDate.toLocaleTimeString())}</span>
      </div>
    ));

    // const listItems =
    const handlers = {
      new: this.createComments,
      esc: this.moveRefToComments,
      delete: this.deleteComments
    };
    return (
      <HotKeys keyMap={keyMap}>
        <HotKeys handlers={handlers}>
          <div>
            {/* All Comments */}
            <div className="navbar">
              <main className="ms-welcome__main">
                <div id="main" tabIndex="0" onKeyDown={this.handleKeyDown}></div>
                <table className="ms-Table">
                  <thead>
                    <tr className="comment-list-names">
                      <th className="User">User</th>
                      <th className="Date">Date</th>
                      <th className="Time">Time</th>
                      <th className="Num">#</th>
                      <th className="Sheet">Sheet</th>
                      <th className="Cell">Cell</th>
                    </tr>
                  </thead>
                  <tbody>
                    {/* <input ></input> */}
                    {this.state.listItems.map((item, index) => {
                      // let ref = React.createRef();
                      return (
                        <tr
                          className={
                            this.state.cursor === index ? "ms-ListItem-active comment-list" : "ms-ListItem comment-list"
                          }
                          id={index}
                          key={index}
                          onClick={e => {
                            this.loadmessages(item.sheet + "!" + item.cell, e);
                            this.setState({
                              cursor: index
                            });
                            // ref.current.focus();
                          }}
                          // ref={ref}
                          // onKeyDown={() => {

                          //   this.handleKeyDown();
                          //   ref.current.focus();
                          // }}
                        >
                          <td className="User">{item.user}</td>
                          <td className="Date">
                            {item.date.getMonth() + "/" + item.date.getDate() + "/" + item.date.getFullYear()}
                          </td>
                          <td className="Time">{properTime(item.date.toLocaleTimeString())}</td>
                          <td className="Num">{item.count}</td>
                          <td className="Sheet">{item.sheet}</td>
                          <td className="Cell">{item.cell}</td>
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
                <div>
                  <DefaultButton onClick={this.createComments}>New</DefaultButton>
                  <DefaultButton onClick={this.deleteComments}>Delete</DefaultButton>
                </div>
              </main>
            </div>

            {/* Messages */}
            {!this.state.newComment ? (
              <Messages
                messages={this.state.messages}
                editMessageHelper={this.editMessageHelper}
                currentWorkingUser={this.state.currentWorkingUser}
              ></Messages>
            ) : (
              <MessageBar messageBarType={MessageBarType.warning}>No Comments Here</MessageBar>
            )}

            {/* TextField */}
            {this.state.currentRange.length ? (
              <div className="text-send">
                <TextField
                  // multiline
                  // resizable={false}
                  className="fill-width"
                  placeholder="Enter a reply"
                  value={this.state.text}
                  onChange={this.setText}
                  // autoFocus
                  componentRef={this.textInput}
                  onKeyDown={e => {
                    if (e.keyCode === 13) {
                      this.state.editMode ? this.editMessage() : this.respondToMessages();
                    }
                    if (e.keyCode === 27) {
                      this.moveRefToComments();
                    }
                  }}
                />
                {/* <IconButton className="ms-welcome__action" iconProps={{ iconName: "Send" }} /> */}
                <DefaultButton
                  onClick={() => {
                    this.state.editMode ? this.editMessage() : this.respondToMessages();
                  }}
                >
                  Send
                </DefaultButton>
              </div>
            ) : (
              <div></div>
            )}
          </div>
        </HotKeys>
      </HotKeys>
    );
  }
}
